// 封装接口方法
// 每个方法负责一个url地址

// 导入封装好的axios实例模块
import request from '@/utils/request.js'

// 导入token获取模块方法
// import { getToken } from '@/utils/token'

// 指定导出获取所有频道的接口请求(这里省略的return和{}，而不是没有)
export const getAllChannelsAPI = () =>
  request({
    url: '/v1_0/channels',
    method: 'GET'
  })

// 指定导出登录接口
export const loginAPI = ({ mobile, code }) => {
  return request({
    url: '/v1_0/authorizations',
    method: 'POST',
    data: {
      mobile,
      code
    }
  })
}

// 刷新token
export const getNewTokenAPI = () => {
  return request({
    url: '/v1_0/authorizations',
    method: 'PUT',
    headers: {
      Authorization: `Bearer ${localStorage.getItem('refresh_token')}`
    }
  })
}

// 获取用户选择的内容列表频道(用户没有登录时，该接口会返回后台设置的默认的频道列表)
export const getUserChannelsAPI = () => request({
  url: '/v1_0/user/channels',
  method: 'GET'
  // 该接口规定可以传请求头，值是token
  // headers: {
  //   Authorization: `Bearer ${getToken()}`
  // }
})

// 用户关注作者
export const userFollowedAPI = ({ userId }) => {
  return request({
    url: '/v1_0/user/followings',
    method: 'Post',
    data: {
      target: userId
    }
  })
}
// 取关
export const userUnFollowedAPI = ({ userId }) => {
  return request({
    url: `/v1_0/user/followings/${userId}`,
    method: 'DELETE'
  })
}

// 文章获取列表
export const articlesListAPI = ({ channel_id, timestamp }) => {
  return request({
    url: '/v1_0/articles',
    method: 'GET',
    // headers: {
    //   Authorization: `Bearer ${getToken()}`
    // },
    params: {
      channel_id,
      timestamp
    }
  })
}

// 不感兴趣文章接口
export const dislikeArticle = ({ artId }) => {
  return request({
    url: '/v1_0/article/dislikes',
    method: 'POST',
    // headers: {
    //   Authorization: `Bearer ${getToken()}`
    // },
    // 在request中我们新增了请求拦截器，判断了请求中是否存在请求头的token，没有会加上，所以这里我们不传请求头过去也可以了
    data: {
      target: artId
    }
  })
}

// 举报文章接口
export const articleReportsAPI = ({ artId, value }) => {
  return request({
    url: '/v1_0/article/reports',
    method: 'POST',
    data: {
      target: artId,
      type: value,
      remark: '附加问题说明,这里写死了不用传参。如果想让用户填入附加内容,在调用该接口时提供弹框接收用户输入内容并传过来'
    }
    // headers: {
    //   Authorization: `Bearer ${getToken()}`
    // }
  })
}

// 文章详情API
export const articleDetailAPI = ({ art_id }) => {
  return request({
    url: `/v1_0/articles/${art_id}`
  })
}

// 文章点赞
export const articleLikeAPI = ({ artId }) => {
  return request({
    url: '/v1_0/article/likings',
    method: 'POST',
    data: {
      target: artId
    }
  })
}
// 取消点赞
export const articleDisLikeAPI = ({ artId }) => {
  return request({
    url: `/v1_0/article/likings/${artId}`,
    method: 'DELETE'
  })
}

// 更新用户频道接口
export const updateChannelAPI = ({ channels }) => {
  return request({
    url: '/v1_0/user/channels',
    method: 'PUT',
    data: {
      channels
    }
  })
}

// 搜索-联想菜单接口
export const suggestListAPI = ({ kw }) => {
  return request({
    url: '/v1_0/suggestion',
    params: {
      q: kw
    }
  })
}

// 搜索 - 结果列表
export const searchResultListAPI = ({ kw, page = 1, per_page = 10 }) => {
  return request({
    url: '/v1_0/search',
    params: {
      q: kw,
      page,
      per_page
    }
  })
}

// 获取文章评论接口
export const commentListAPI = ({ artId, offset = null, limit = 10 }) => {
  return request({
    url: '/v1_0/comments',
    params: {
      type: 'a',
      source: artId,
      offset,
      limit
    }
  })
}

// 评论 - 喜欢
export const commentLikingAPI = ({ comId }) => {
  return request({
    url: '/v1_0/comment/likings',
    method: 'POST',
    data: {
      target: comId
    }
  })
}
// 评论-取消喜欢
export const commentDisLikingAPI = ({ comId }) => {
  return request({
    url: `/v1_0/comment/likings/${comId}`,
    method: 'DELETE'
  })
}

// 发布评论
export const sendCommentAPI = ({ artId, content, art_id = null }) => {
  // 根据情况用户可传可不传参时的处理，这里的第三个参数可传可不传就不能写死，根据用户传来的参数判断在发起请求。
  const obj = {
    target: artId,
    content: content
  }
  if (art_id !== null) {
    obj.art_id = art_id
  }
  return request({
    url: '/v1_0/comments',
    method: 'POST',
    data: obj
  })
}

// 用户个人资料(关注、动态、粉丝等信息)
export const userInfoAPI = () => {
  return request({
    url: '/v1_0/user'
  })
}
// 用户个人信息资料（生日等）
export const userProfileAPI = () => {
  return request({
    url: '/v1_0/user/profile'
  })
}

// 修改头像
export const updateUserPhotoAPI = (fd) => {
  return request({
    url: '/v1_0/user/photo',
    method: 'PATCH',
    data: fd // fd是请求时传来的表单对象，而不是一个普通的对象格式(因为这里涉及到头像文件的传递)
  })
}

// 用户 - 更新资料
export const updateProfileAPI = ({ birthday, userName }) => {
  return request({
    url: '/v1_0/user/profile',
    method: 'PATCH',
    data: {
      birthday: birthday,
      name: userName
    }
  })
}
