// 利用axios封装数据方法

import theAxios from 'axios'
import { Notify } from 'vant'

// 导入路由模块
import router from '@/router/index'
// // 导入获取token模块方法
import { getToken, removeToken, setToken } from '@/utils/token'

import { getNewTokenAPI } from '@/api/index'

// 创建axios实例
const axios = theAxios.create({
  // 请求根路径
  baseURL: 'http://toutiao.itheima.net',
  // 请求超时时间
  timeout: 20000
})

// 添加请求拦截器
axios.interceptors.request.use(
  function (config) {
    // 在发送请求之前做些什么
    // 判断本地是否有token,以及是否携带请求头
    // ?. 可选链操作符，如果?前面没有后面的length，没有直接返回undefined不执行该判断中代码。有length才会调用length方法，否则空值调用length方法会报错
    if (getToken()?.length > 0 && config.headers.Authorization === undefined) {
      // 发起请求之前, 把token携带在请求头上(表明自己身份)
      config.headers.Authorization = `Bearer ${getToken()}`
    }
    return config
  },
  function (error) {
    // 对请求错误做些什么
    return Promise.reject(error)
  }
)

// axios响应的拦截器，要写在导出实例的前面
// axios.interceptors.response.use()里面是俩函数
axios.interceptors.response.use(
  function (response) { // 当状态码为2xx/3xx开头的进这里
    // 对响应数据做点什么
    return response
  },
  // 响应状态码4xx/5xx进这里
  async function (error) {
    // 状态码是401表示身份过期，用户需要重新登录
    if (error.response.status === 401) {
      // 这里不能用this.$router.push()，这里的this没有指向.所以我们在上面直接导入路由模块，使用其方法跳转
      // Notify({ type: 'warning', message: '身份认证过期 ，需重新登录' })

      removeToken()
      // 刷新token，不要再以前那样清除过期的token，强制跳转到登录页
      const { data: res } = await getNewTokenAPI()
      console.log(res)
      setToken(res.data.token)
      // error.config是上一次请求的配置对象，把旧的请求头的token换成最新的
      error.config.headers.Authorization = `Bearer ${res.data.token}`
      // 重新发起上次由于token过期而未成功的请求
      return axios(error.config)

      // router.replace('/login')
      // 提示：this.$router就是我们在router模块写好导出的对象实例，所以可以这样用。尝试去打印对比就知道了
    } else if (error.response.status === 500 && error.config.url === '/v1_0/authorizations' && error.config.method === 'put') {
      // 刷新的refresh——token也过期了，必须重新登录
      removeToken()
      localStorage.clear()
      Notify({ type: 'warning', message: '身份认证过期 ，需重新登录' })
      router.replace('/login')
    }

    return Promise.reject(error)
  }
)

// 向外导出axios请求实例（调用该模块时，传入的参数除了url，其他都设有默认值）最终返回的是axios，根据传入的参数发起相关请求
// 注意这里的形参在{},结构赋值，传来的参数名和此一致就行，而不是普通传参根据的顺序
export default ({ url, method = 'GET', params = {}, data = {}, headers = {} }) => {
  return axios({
    url,
    method,
    params,
    data,
    headers
  })
}
